declare global {
    interface Window {
        _coreCache: {
            element_counter: number;
        };
    }
}

export class Persistent {
    constructor() {
        // use window so HMR dont kill it
        if (!window._coreCache) {
            window._coreCache = {
                element_counter: 0
            };
        }
    }

    newID() {
        window._coreCache.element_counter++;
        let current = window._coreCache.element_counter;
        switch (`${current}`.length) {
            case 1:
                return `0000${current}`;
            case 2:
                return `000${current}`;
            case 3:
                return `00${current}`;
            case 4:
                return `0${current}`;
            case 5:
                return `0${current}`;
            default:
                return `${current}`;
        }
    }
}
