import {
    customElement,
    inject,
    property,
    subscribe,
    unSubscribe
} from '@simple-html/core';
import { html } from 'lit-html';
import { Persistent } from 'src/states/persistent';
import { EVENTS } from 'src/states/events';

@inject(Persistent)
@customElement('element-2')
export default class extends HTMLElement {
    private _id: string;
    private additinalClassesText = 'bg-indigo-400';
    @property() count = '0';
    private first = true;

    constructor(private persistent: Persistent) {
        super();
        this._id = this.persistent.newID();
        console.log(`[${this.tagName} : ${this._id}]`, 'constructor');
    }

    connectedCallback() {
        console.log(`[${this.tagName} : ${this._id}]`, 'connectedCallback');
        subscribe(EVENTS.COUNTER2, this, this.counterEvent);
    }

    counterEvent(result: string) {
        this.count = result;
    }

    disconnectedCallback() {
        console.log(`[${this.tagName} : ${this._id}]`, 'disconnectedCallback');
        unSubscribe(EVENTS.COUNTER2, this);
    }

    updated() {
        if (this.first) {
            console.log(`[${this.tagName} : ${this._id}]`, 'updated');
        }
    }

    render() {
        if (this.first) {
            this.first = false;
            console.log(`[${this.tagName} : ${this._id}]`, 'render');
        }
        return html`
            <div class="flex flex-col">
                <p
                    class="flex-1 m-2 p-2 border border-gray-400 ${this
                        .additinalClassesText || ''} text-center"
                >
                    ${this.tagName} <br />
                    ${this.count}
                </p>
                <element-3
                    class="flex-1 m-2 p-2 border border-gray-400 bg-gray-400"
                ></element-3>
            </div>
        `;
    }
}
